<template>
    <div>
        <div class="box">
            <h2>基础用法</h2>
            <el-alert
                title="成功提示的文案"
                type="success">
            </el-alert>
            <el-alert
                title="消息提示的文案"
                type="info">
            </el-alert>
            <el-alert
                title="警告提示的文案"
                type="warning">
            </el-alert>
            <el-alert
                title="错误提示的文案"
                type="error">
            </el-alert>
        </div>
        <div class="box">
            <h2>自定义关闭按钮</h2>
            <el-alert
                title="不可关闭的 alert"
                type="success"
                :closable="false">
            </el-alert>
            <el-alert
                title="自定义 close-text"
                type="info"
                close-text="知道了">
            </el-alert>
            <el-alert
                title="设置了回调的 alert"
                type="warning"
                @close="hello">
            </el-alert>
        </div>
        <div class="box">
            <h2>带有 icon</h2>
            <el-alert
                title="成功提示的文案"
                type="success"
                show-icon>
            </el-alert>
            <el-alert
                title="消息提示的文案"
                type="info"
                show-icon>
            </el-alert>
            <el-alert
                title="警告提示的文案"
                type="warning"
                show-icon>
            </el-alert>
            <el-alert
                title="错误提示的文案"
                type="error"
                show-icon>
            </el-alert>
        </div>
        <div class="box">
            <h2>带有 icon 和辅助性文字介绍</h2>
            <el-alert
                title="成功提示的文案"
                type="success"
                description="文字说明文字说明文字说明文字说明文字说明文字说明"
                show-icon>
            </el-alert>
            <el-alert
                title="消息提示的文案"
                type="info"
                description="文字说明文字说明文字说明文字说明文字说明文字说明"
                show-icon>
            </el-alert>
            <el-alert
                title="警告提示的文案"
                type="warning"
                description="文字说明文字说明文字说明文字说明文字说明文字说明"
                show-icon>
            </el-alert>
            <el-alert
                title="错误提示的文案"
                type="error"
                description="文字说明文字说明文字说明文字说明文字说明文字说明"
                show-icon>
            </el-alert>
        </div>
    </div>
</template>
<script>
export default {
    methods: {
        hello () {
            alert('Hello World!')
        }
    }
}
</script>
<style scoped>
.el-row {
    margin-bottom: 20px;
    &:last-child {
        margin-bottom: 0;
    }
}
.el-col {
    border-radius: 4px;
}

.box {
    margin-bottom: 20px;
}
.el-alert {
    margin-bottom: 20px;
}
</style>
/*按钮样式不用看*/
<style>
/*按钮总样式覆盖*/
.el-button {
    font-size: 12px;
    padding: 9px 16px;
    border-radius: 2px;
}
/*按钮size样式*/
.el-button--medium {
    padding: 0px 16px;
    height: 40px;
    line-height: 38px;
    font-size: 14px;
    min-width: 104px;
}
.el-button--medium.is-round {
    padding: 0px 20px;
}
.el-button--mini {
    padding: 0px 8px;
    height: 24px;
    line-height: 22px;
    font-size: 12px;
    min-width: 56px;
}
.el-button--mini.is-round {
    padding: 0px 15px;
}
/* 朴素按钮的颜色覆盖*/
.el-button.is-plain:focus,
.el-button.is-plain:hover {
    outline: none;
    text-decoration: none;
    color: #1acac6;
    background: #FFF;
    border-color: #1acac6;
}
/*禁止类按钮样式*/
.el-button.is-disabled,
.el-button.is-disabled:focus,
.el-button.is-disabled:hover {
    cursor: not-allowed;
    background: #f8f8f8;
    color: #ccc;
    border: 1px solid #ebeef5;
    -ms-transform: none;
    transform: none;
}
/*primary 类按钮样式修改*/
.el-button--primary {
    color: #fff;
    background-color: #00C4C0;
    border-color: #00C4C0;
}
.el-button--primary:focus, .el-button--primary:hover {
    background: #1acac6;
    border-color: #1acac6;
    color: #fff;
}
/*text样式覆盖*/
.el-button--text.is-disabled,
.el-button--text.is-disabled:focus,
.el-button--text.is-disabled:hover {
    border-color: transparent;
    color: #c0c4cc;
    cursor: not-allowed;
    background-image: none;
    background-color: #fff;
}
.el-button--text {
    color: #00C4C0;
}
.el-button--text:active,
.el-button--text:focus,
.el-button--text:hover {
    color: #1acac6;
    border-color: transparent;
    background-color: transparent;
}
/*危险累按钮样式覆盖*/
.el-button--danger.is-plain {
    color: #F86B6B;
    background: #fff;
    border-color: #F86B6B;
}
.el-button--danger.is-plain:focus, .el-button--danger.is-plain:hover {
    background: #fff;
    border-color: #f97a7a;
    color: #f97a7a;
}
/*loading按钮样式*/
.el-button.is-loading {
    cursor: not-allowed;
    background: #f8f8f8;
    color: #ccc;
    border: 1px solid #eee;
    -ms-transform: none;
    transform: none;
}
</style>

<style>
/*带icon的样式覆盖*/
.el-alert {
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.07);
    border-radius: 4px;
    padding: 12px 16px;
}
.el-alert__title {
    font-size: 12px;
    line-height: 18px;
}
.el-alert__closebtn {
    top: 16px;
}
.el-alert__closebtn.is-customed {
    top: 16px;
}
.el-alert__closebtn:hover {
    color: #333;
}
.el-alert--success {
    background: #F8FFF0;
    border: 1px solid #B7EB94;
    color: #666;
}
.el-alert--success .el-alert__icon {
    color: #93DD68;
}
.el-alert--warning {
    background: #FEFCF0;
    border: 1px solid #FFCA61;
    color: #666;
}
.el-alert--warning .el-alert__icon {
    color: #FFB539;
}
.el-alert--error {
    background: #FFF2F0;
    border: 1px solid #FF9A96;
    color: #666;
}
.el-alert--error .el-alert__icon {
    color: #F86B6B;
}
.el-alert--info {
    background: #E6F7FF;
    border: 1px solid #91D5FF;
    color: #666;
}
.el-alert--info .el-alert__icon {
    color: #54A8F7;
}
/*带title的样式*/
.el-alert__title.is-bold {
    font-size: 16px;
    color: rgba(0, 0, 0, 0.85);
    margin-top: 3px;
    margin-bottom: 7px;
    font-weight: 600;
    line-height: 22px;
    display: block;
}
.el-alert__icon.is-big {
    font-size: 24px;
    position: relative;
    top: -5px;
    margin-left: 8px;
    margin-right: 12px;
}
.el-alert .el-alert__description {
    line-height: 17px;
    color: #666;
}
</style>



